.accordionWrap {
    background: #F9F5FF;
}

.accordionTitle {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: 600;
    font-size: 22px;
    line-height: 24px;
    color: #101828;
    cursor: default;

}

.accordion {
    border-top: 1px solid #EAECF0;
    border-bottom: 1px solid #EAECF0;
}

.accordionTitle div {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    color: #7F56D9;
}

.accordionTitle span {
    width: 600px;
    margin: 20px 0;
}


.accordionContent {

}

.circle {
    background: none;
    border-radius: 50%;
    border: 1px solid #7F56D9;
}